<template lang="html">
  <div class="">
    <div v-for="list in news">
      <div class="newsContentTitle">
        {{list.title}}
      </div>
      <div class="newsSource">
        {{list.source}}&nbsp;{{list.pubDate}}
      </div>
      <!-- <div class="newsContentContent" v-html="list.html">
      </div> -->
      <div class="newsContentContent">
      <div v-for="(item,index) in list.allList">
        <div :style="{'text-align':item.url!=undefined?'center':'none'}">
          <p v-if="item.url==undefined">{{item}}</p>
          <img class="newsImg" :src=item.url v-if="item.url!=undefined" @click="bigImg(item.url)">
        </div>
      </div>
      </div>
    </div>
    <div class="beLikeNews">
      推荐新闻区开发中，敬请期待
    </div>

    <bigimg v-if="showPic" :imgSrc="newsImg" @close = "close"></bigimg>
  </div>
</template>

<script>
import bigimg from '../components/bigImg'
export default {
  data() {
    return {
      title: '',
      id:'',
      news:[],
      showPic:false,
      newsImg:''
    }
  },
  components:{
    'bigimg':bigimg
  },
  methods: {
    getContent() {
      this.$axios.get('http://route.showapi.com/109-35', {
        params:{
          "showapi_timestamp": this.formatterDateTime,
          "showapi_appid": '93017', //这里需要改成自己的appid
          "showapi_sign": 'a0ec01b1d23241d8a3b643afa058c715', //这里需要改成自己的应用的密钥secret
          "title": this.title,
          "needContent": "0",
          "id": this.id,
          "needHtml":"1",
          "needAllList":"1"
        }

      }).then(res => {
        if(res.status == 200){
          this.news = res.data.showapi_res_body.pagebean.contentlist;
        }
      }).catch(err => {

      })
    },
    bigImg(image){
      this.showPic = true;
      this.newsImg = image;
    },
    close(){
      this.showPic = false;
    }

  },
  mounted() {
    this.title = this.$route.query.title;
    this.id = this.$route.query.id;
    this.getContent();
  }
}
</script>

<style lang="scss">
.newsContentTitle{
  text-align: center;
  font-size: 24px;
  width: 100%;
  height: 1.5rem;
}
.newsSource{
  font-size: 16px;
}
.newsContentContent div:has(> img){
  height: 7rem;
}
.newsContentContent p{
  text-indent:34px;
  letter-spacing: 1px;
  font-size: 18px;
}
.showPic{
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 999;
  background-color: red;
}
.beLikeNews{
  text-align: center;
  line-height: 2rem;
  height: 2rem;
  border-top: 1px solid #F8F8FF;
  font-size: 14px;
}
@media screen and (max-width: 321px){

  .newsImg{
    max-width: 5rem;
  }

}

@media screen and (min-width: 321px) and (max-width:400px){
  .newsImg{
    max-width: 6.2rem;
  }


}

@media screen and (min-width: 400px){

  .newsImg{
    max-width: 6.4rem;
  }
}

</style>



