<template>
    <!-- 过渡动画 -->
    <transition name="fade">
        <div class="img-view" >
            <!-- 遮罩层 -->
            <div class="img-layer" @click="closeImg"></div>
            <div class="img">
                <img :src="imgSrc">
            </div>
        </div>
    </transition>
</template>
<script>
export default {
    props: ['imgSrc'],
    methods: {
        closeImg() {
            // 发送事件
            this.$emit('close')
        }
    }
}
</script>
<style scoped>
/*动画*/
.fade-enter-active,
.fade-leave-active {
    transition: all .2s linear;
    transform: translate3D(0, 0, 100%);
}

.fade-enter,
.fade-leave-active {
    transform: translate3D(0,100%, 0);
}


/* bigimg */

.img-view {
    position: fixed;
    width: 100%;
    height: 100%;
    top:25%;
}

/*遮罩层样式*/
.img-view .img-layer {
    position: fixed;
    z-index: 999;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.7);
    width: 100%;
    height: 100%;
    overflow: hidden;
}

/*不限制图片大小，实现居中*/
.img-view .img img {
    max-width: 100%;
    display: block;
    position: absolute;
    left: 0;
    right: 0;
    margin: auto;
    z-index: 1000;
}
</style>



