import Vue from 'vue'
import Router from 'vue-router'
import index from '@/pages/index'
import news from '@/pages/news'
import newsContent from '@/pages/newsContent'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/news',
      name: 'news',
      component: news
    },
    {
      path: '/news/newsContent',
      name: 'newsContent',
      component: newsContent
    }
  ]
})



