<template lang="html">
<div class="container">
  <!-- <div class="headerSearch">
    <input type="text" placeholder="你想搜索什么"><button type="button" name="button">搜索</button>
  </div> -->
  <div class="newsBox" v-for="list in newsList" @click="toContent(list.title,list.id)">
    <div class="newsTitle">
      {{list.title}}
    </div>
    <div class="newsContent" v-if="list.havePic">
        <img :src=img.url class="newsImg" v-for="(img,index) in list.imageurls" v-if="index<3">
    </div>
    <div class="newsDetail">
      {{list.source}} &nbsp; {{list.pubDate}}
    </div>
  </div>
  <div class="more" style="text-algin=center;" @click="nextPage">
    <a>点击加载更多</a>
  </div>
</div>
</template>

<script>
export default {
  data() {
    return {
      id: '',
      name: '',
      page: 1,
      newsList: []
    }
  },
  methods: {
    getNewsList() {
      this.$axios.get('http://route.showapi.com/109-35', {
        params: {
          "showapi_timestamp": this.formatterDateTime,
          "showapi_appid": '93017', //这里需要改成自己的appid
          "showapi_sign": 'a0ec01b1d23241d8a3b643afa058c715', //这里需要改成自己的应用的密钥secret
          "channelId": this.id,
          "channelName": this.name,
          "page": this.page,
          "needContent": "0",
          "needAllList": "0",
          "maxResult": "20",
          "id": ""
        }
      }).then(res => {
        if (res.data.showapi_res_code == 0) {
          let list = res.data.showapi_res_body.pagebean.contentlist
          for (var i = 0; i < list.length; i++) {
            this.newsList.push(list[i])
          }
          console.log(this.newsList);
        }
      }).catch(err => {
        console.log('获取失败');
      })
    },
    nextPage() {
      this.page += 1
      this.getNewsList();
    },
    toContent(gettitle,getid){
      this.$router.push({
        name:'newsContent',
        query:{
          title:gettitle,
          id:getid
        }
      })
    }
  },
  mounted() {
    this.id = this.$route.query.id;
    this.name = this.$route.query.name;
    this.getNewsList();
  }
}
</script>

<style lang="scss">
.container {
    width: 100%;
    overflow-x: hidden;
    background-color: #F2F2F2;
        .newsBox {
          background-color: #ffffff;
            padding: 0.1rem;
            border: #F8F8FF solid 1px;
            margin: 15px 6px 8px 6px;
            border-radius: 8px;
            box-shadow: 0 0 10px -3px #000;
            .newsTitle {
                min-height: 1.2rem;
                display: flex;
                align-items: center;
                padding: 0.14rem;
            }
            .newsContent {
                display: flex;
                padding: 0.14rem;
                .newsImg {
                    flex: 1;
                    width: 2rem;
                    height: 2rem;
                    margin: 5px;
                }
                .newsImg1{
                  flex: 1;
                  min-height: 3rem;
                }
            }
            .newsDetail{
              font-size: 12px;
              color: #D3D3D3;
            }

        }
    }
    .more{
      width: 100%;
      text-align: center;
    }
</style>



