<template lang="html">
  <div>
    <div class="channelHeader">
      {{showContent?'天气预报':'每日新闻'}}
    </div>
  <div class="channelBox" v-if="showContent!=true">
    <div class="" v-for="list in channelList" >
      <div class="channelItem" @click="getId(list.channelId,list.name)">
        {{list.name}}
      </div>
    </div>
  </div>

  <div class="weather" v-if="showContent">
    <div class="weatherContent" v-if="loading">
    <div class="weatherDetail" :style="{'background':'url('+todayWeatherList.now.weather_pic+')','background-repeat': 'no-repeat',
    'background-size': '100% 100%'}">
      <li>{{todayWeatherList.now.aqiDetail.area}}</li>
      <li>{{todayWeatherList.now.weather}}</li>
      <li>{{todayWeatherList.now.temperature}}°</li>
    </div>
    <div class="todayWeather">
      <!--  今天天气-->
      <ul>
        <li>{{weekday(todayWeatherList.f1.weekday)}}</li>
        <li style="font-size:16px;">今天</li>
        <li style="float:right">{{todayWeatherList.f1.day_air_temperature}}° &nbsp;{{todayWeatherList.f1.night_air_temperature}}°</li>
      </ul>
        <ul>
          <li>空气湿度</li>
          <li style="float:right">{{todayWeatherList.now.sd}}</li>
        </ul>
        <ul class="borderBottom">
          <li>{{todayWeatherList.now.wind_direction}}</li>
          <li style="float:right">风力{{todayWeatherList.now.wind_power}}</li>
        </ul>
        <!-- 其他日期 -->
        <div class="otherWeather">
          <div class="">
            <img :src=todayWeatherList.f2.day_weather_pic>
          </div>
          <div class="borderBottom">
            <ul>
              <li>{{weekday(todayWeatherList.f2.weekday)}}</li>
              <li>{{todayWeatherList.f2.day_weather}}  </li>
            </ul>
            <ul style="font-size:14px">
              <li>白天气温:{{todayWeatherList.f1.day_air_temperature}}° </li>
              <li>晚间气温:{{todayWeatherList.f1.night_air_temperature}}°</li>
            </ul>
          </div>
        </div>

        <div class="otherWeather">
          <div class="">
            <img :src=todayWeatherList.f3.day_weather_pic>
          </div>
          <div class="borderBottom">
            <ul>
              <li>{{weekday(todayWeatherList.f3.weekday)}}</li>
              <li>{{todayWeatherList.f3.day_weather}}  </li>
            </ul>
            <ul style="font-size:14px">
              <li>白天气温:{{todayWeatherList.f3.day_air_temperature}}° </li>
              <li>晚间气温:{{todayWeatherList.f3.night_air_temperature}}°</li>
            </ul>
          </div>
        </div>

    </div>
    </div>

  </div>
  <div class="footerTab">
    <li v-for="(item,index) in 2" @click="toggleTab(index)">{{index==0?'新闻':'天气'}}</li>
  </div>
    </div>
</template>

<script>
export default {
  data() {
    return {
      channelList: [],
      todayWeatherList: {},
      showContent: false,
      loading: false,
    }
  },
  methods: {
    getNews() {
      this.$axios.get('http://route.showapi.com/109-34', {
        params: {
          "showapi_timestamp": this.formatterDateTime,
          "showapi_appid": '93017', //这里需要改成自己的appid
          "showapi_sign": 'a0ec01b1d23241d8a3b643afa058c715', //这里需要改成自己的应用的密钥secret
        }
      }).then(res => {
        if (res.status == 200) {
          this.channelList = res.data.showapi_res_body.channelList
        }

      }).catch(err => {
        console.log("erro");
      });
    },
    getWeather() {
      this.$axios.get('http://route.showapi.com/9-4', {
        params: {
          "showapi_timestamp": this.formatterDateTime,
          "showapi_appid": '93017', //这里需要改成自己的appid
          "showapi_sign": 'a0ec01b1d23241d8a3b643afa058c715', //这里需要改成自己的应用的密钥secret
          "ip": "",
          "needMoreDay": "0",
          "needIndex": "0",
          "needHourData": "0",
          "need3HourForcast": "0",
          "needAlarm": "0"
        }
      }).then(res => {
        if (res.status == 200) {
          this.loading = true
          let data = res.data.showapi_res_body
          this.todayWeatherList = data
          console.log(data);
        }

      }).catch(err => {
        console.log("erro");
      });
    },
    getId(id, name) {
      this.$router.push({
        name: 'news',
        query: {
          id: id,
          name: name
        }
      })
    },
    toggleTab(index) {
      switch (index) {
        case 0:
          this.showContent = false
          break;
        case 1:
          this.showContent = true
          this.getWeather();
          break;
        default:
          0
      }
    },
    weekday(day) {
      switch (day) {
        case 1:
          return '星期一'
          break;
        case 2:
          return '星期二'
          break;
        case 3:
          return '星期三'
          break;
        case 4:
          return '星期四'
          break;
        case 5:
          return '星期五'
          break;
        case 6:
          return '星期六'
          break;
        case 7:
          return '星期日'
          break;
      }
    }
  },
  beforecreate() {
    console.log('aaaa');
  },
  mounted() {
    this.getNews();

  },
  computed: {

  }
}
</script>

<style lang="scss">
.channelHeader {
    width: 100%;
    height: 1rem;
    background-color: #00BFFF;
    text-align: center;
    line-height: 1rem;
    font-weight: 800;
}
.channelBox {
    width: 100%;
    height: 100%;
    flex-flow: row wrap;
    display: flex;
    justify-content: center;

    .channelItem {
        font-size: 16px;
        display: flex;
        flex: 1;
        justify-content: center;
        align-items: center;
        width: 2.1rem;
        height: 2.1rem;
        border: #F0F8FF solid 1px;
        margin: 0.1rem;
    }
}
.footerTab {
    border-top: #F8F8FF solid 1px;
    height: 1rem;
    width: 100%;
    background-color: #FAFAFA;
    position: fixed;
    bottom: 0;
    display: flex;
}
.footerTab li {
    flex: 1;
    text-decoration: none;
    display: inline-block;
    line-height: 1rem;
    text-align: center;
}
.weatherContent {
    height: 100vh;
    list-style: none;
    .weatherDetail{
          text-align: center;
        height: 4rem;
        width: 100%;
    }
    .todayWeather{
      padding: 10px;
    }
    .todayWeather li{
      display:inline-block;
    }
    .otherWeather{
      display: flex;
    }

}
.weatherDetail li:nth-child(1) {
    font-size: 32px;
}
.weatherDetail li:nth-child(2) {
    font-size: 14px;
}
.weatherDetail li:nth-child(3) {
    font-size: 48px;
}
.weatherDetail li:nth-child(4) {
    font-size: 18px;
}

.borderBottom{
  border-bottom:#F8F8FF solid 1px
}
</style>



